.document_card {
  width: 100%;
  height: 82px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0.7rem;
  padding: 1rem;
  margin-bottom: 0.8rem;
}
.document_card_text > p {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: rgba(18, 18, 18, 0.8);
  margin-bottom: 0.1rem;
}
.menu_icon {
  cursor: pointer;
  position: absolute;
  right: 3%;
}
.apf_container {
  padding: 0.8vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.3px solid rgb(222, 214, 214);
  cursor: pointer;
  border-bottom: 0.5px solid #eaeaea;
}
.apf_container_inner {
  display: flex;
  gap: 0.5vw;
  align-items: center;
}
.apf_container:hover {
  background: rgba(75, 26, 133, 0.1);
}
.apf_container_inner > img {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
}
.apfci_text > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  margin-bottom: 0;
  color: rgba(18, 18, 18, 0.5);
}
.apfci_text > h6 {
  font-weight: 500;
  font-size: 0.9rem;
  line-height: 17px;
  margin-bottom: 0.3vw;
  color: #121212;
}
