.af_container {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 2%;
  flex-wrap: wrap;
}

.af_container_inner {
  flex: 1 1 32%;
  max-width: 32%;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 0.6rem 0 0 0;
  position: relative;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 980px) {
  .af_container_inner {
    flex: 1 1 49%;
  }
}
.afc_collapse {
  text-align: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  color: var(--t-color-collapse);
  cursor: pointer;
}
.afc_collapse > img {
  margin-left: 0.6rem;
  cursor: pointer;
  height: 1.5rem;
  width: auto;
}
.safcst_container {
  padding: 1rem;
  border-radius: 6px;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  background: #fff;
  box-shadow: 0px 4px 4px 0px #0057ff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-inline: 0.5rem;
}
.safcst_container_inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.safcst_container_inner > p {
  margin: 0;
  color: #121212;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 0.2rem;
}
.safcst_container_inner > h6 {
  margin: 0;
  color: #121212;
  font-size: 14px;
  font-weight: 600;
  border-radius: 2px;
  border: 0.5px solid rgba(18, 18, 18, 0.25);
  padding: 0.3rem 1rem;
  width: auto;
}
.af_heading {
  text-align: center;
  color: #121212;
  font-size: 20px;
  font-weight: 600;
}
.af_top_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.af_top_container > p {
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  cursor: pointer;
  color: #0000ff;
  margin: 0;
}
/* // */

.edit_icon {
  cursor: pointer;
  height: 1.5rem;
  width: auto;
}
.admission_fees_container {
  width: 96.4%;
  min-height: 200px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  margin: 1.2rem;
}

.admission_fees_amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Inter";
  font-style: normal;
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  margin-top: 0.7rem;
}
.admission_fees_amount > h6,
.admission_fees_amount_top > h6 {
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #121212;
}
.admission_fees_amount_top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-family: "Inter";
  font-style: normal;
  gap: 0.5rem;
}
.admission_fees_grant {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Inter";
  font-style: normal;
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  margin-bottom: 0.4rem;
}

.admission_fees_grant > h6 {
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: rgba(18, 18, 18, 0.5);
}

.pending_btn,
.pending_btn:focus {
  width: 96.4%;
  height: 41px;
  background: #002bbe;
  border-radius: 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #ffffff;
  outline: none;
  border: none;
  margin: 1.2rem;
}
.paid_btn,
.paid_btn:focus {
  width: 96.4%;
  height: 41px;
  background: rgba(32, 176, 56, 0.15);
  border: 1px solid #20b038;
  border-radius: 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #20b038;
  margin: 1.2rem;
  outline: none;
}

.refund_btn,
.refund_btn:focus {
  width: 96.4%;
  height: 41px;
  background: rgba(18, 18, 18, 0.5);
  border: 1px solid #000000;
  border-radius: 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #ffffff;
  margin: 1.2rem;
  outline: none;
}

.card_title {
  font-family: "Inter";
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1.1rem;
  position: relative;
}

.card_title > h6 {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
}

.card_title > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.5);
}

.collect_btn,
.collect_btn:focus {
  width: 117px;
  height: 25px;
  background: #002bbe;
  border-radius: 4px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
  border: none;
  outline: none;
}

.add_remark {
  margin-top: 0.7rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
.add_remark_btn {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #002bbe;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
}

.add_remark_text {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #800080;
  word-break: break-all;
  white-space: break-spaces;
}

.remark_modal {
  padding: 1rem;
}
.save_remark,
.save_remark:focus {
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  height: 48px;
  background: rgba(0, 26, 114, 0.15);
  border: 1px solid #001a72;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #001a72;
  outline: transparent;
}

.remark_modal_title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #121212;
  border-bottom: 1px solid rgba(18, 18, 18, 0.3);
  padding-bottom: 0.6rem;
}
.edit_icon {
  position: absolute;
  right: 1.7%;
}

/* // new application card */
.student_admission_fees_card_container {
  width: 32%;
  min-height: 200px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 0.6rem 0 0 0;
  position: relative;
  font-family: "Inter";
  font-style: normal;
  margin-bottom: 1rem;
}
.student_admission_card_title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 1rem;
}
.student_admission_card_title > h6 {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
}
.student_admission_card_title > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.5);
  display: flex;
  flex-wrap: wrap;
}

.student_admission_count_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Inter";
  font-style: normal;
  margin-top: 0.8rem;
  padding: 0 1rem;
}
.student_admission_count {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.student_admission_count > h5 {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #121212;
  margin: 0;
}
.student_admission_count > h6 {
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #121212;
  margin: 0;
}
.student_admission_count_image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.student_admission_count_image > img {
  width: 100px;
  height: 100px;
}
.show_text {
  position: absolute;
  top: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.show_text > h6 {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #121212;
  margin: 0;
}

.student_admission_cash_card {
  width: 100%;
  /* min-height: 96px; */
  background: #ffffff;
  border-top: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 2px 2px 8px 8px;
  margin-top: 0.6rem;
}

.view_receipt_btn,
.view_receipt_btn:focus {
  width: 100%;
  height: 32px;
  background: #def3e1;
  border-radius: 0px 0px 8px 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #008000;
  border: none;
  outline: transparent;
}

.student_admission_collect_btn,
.student_admission_collect_btn:focus {
  width: 100%;
  height: 32px;
  background: #002bbe;
  border-radius: 0px 0px 8px 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
  border: none;
  outline: transparent;
}

.remark_card_container {
  margin-top: 0.5rem;
  width: 100%;
  min-height: 88px;
  background: #ffffff;
  border-top: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 2px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.image_refund_btn,
.image_refund_btn:focus {
  width: 108px;
  height: 25px;
  background: #121212;
  border-radius: 2px;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
  border: none;
  outline: transparent;
  margin-top: 1.07rem;
}
.add_scholar_number_btn,
.add_scholar_number_btn:focus {
  width: 100%;
  min-height: 29px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.6);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  padding: 7px 14px;
  font-weight: 400;
  font-size: 11px;
  line-height: 12px;
  text-align: center;
  color: #0000ff;
  outline: transparent;
}

/* // for student admission profile search  */
.saps_container {
  min-height: 10rem;
  margin-top: -40px;
  z-index: 1300;
  display: block;
  position: absolute;
  background: #ffffff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  min-width: calc(100% - 0rem);
  max-width: 35rem;
  max-height: 26.4rem;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: -2px 13px 21px -9px rgba(0, 0, 0, 0.75);
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth;
}
